import { Row, Col } from "react-bootstrap";
import { Route, useRouteMatch, useParams } from "react-router-dom";

import DataMenu from "../data/DataMenu";
import Topic from "./Topic";

function Chapter(props) {
  const { chapter_name } = useParams();
  const { url } = useRouteMatch();

  return (
    <div>
      <Route exact path={url} />

      <Route path={`${url}/topic/:topic_name`}>
        <Topic chapter={chapter_name} />
      </Route>
    </div>
  );
}

export function Testing() {
  const { url } = useRouteMatch();

  return (
    <Row>
      <Col>
        <Route exact path={url}>
          <Row>
            <Col xs={12} sm={12} md={12} lg={3} xl={3}>
              <DataMenu />
            </Col>
            <Col>
              <h4>РЕЖИМ ТЕСТИРОВАНИЯ</h4>
              <p>
                Это рисунок, вопрос к нему и ряд ответов (от 2 до 8), среди которых один правильный. Попытайтесь сформулировать ответ <span className="text-danger">(это ключевой момент – самому сформулировать правильный ответ!!!)</span> и только тогда среди предложенных ответов ищите правильный. Жмите на выбранный ответ и если он становиться зеленым – значит, ответили верно, а если красным – значит ошиблись.
                Программа позволяет оценивать (в 5 и 10 бальной системе) результаты теста. Для этого перед началом тестирования нажмите на кнопку "Старт", а в конце нажмите "Оценка".
                <br />
                <br />
                Текстовые поля продублированы звуком, который можно подключать, используя аудиоплейеры. Щелкнув по рисунку, можно увеличить его и рассмотреть детали. Отдельные вопросы проиллюстрированы дополнительными рисунками. Отдельные вопросы проиллюстрированы видеофрагментами. Панель управления видео позволяет разворачивать видео на весь экран.
                <br />
                <br />
                В комментариях к вопросам могут быть ссылки на различные дополнительные материалы. Чтобы открыть ссылку нужно её выделить, скопировать (Ctrl+C), вставить (Ctrl+V) в адресную строку браузера и нажать Enter. Если экран сенсорный, то долгое нажатие на ссылке выделяет её, а в открывшемся окне меню нажмите на иконку YouTube или иконку браузера.
                <br />
                <br />
                Для увеличения окна с программой используйте "ctrl +" , для уменьшения "ctrl -".
              </p>
            </Col>
          </Row>
        </Route>

        <Route path={`${url}/chapter/:chapter_name`}>
          <Chapter />
        </Route>
      </Col>
    </Row>
  );
}

export default Testing;
